import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { RxApplicationLoaderService } from './application-loader.service';

@Injectable({
  providedIn: 'root'
})
export class RxApplicationLoaderResolver implements Resolve<void> {
  constructor(private rxApplicationLoaderService: RxApplicationLoaderService) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): void {
    this.rxApplicationLoaderService.removeApplicationLoader();
  }
}
